<template>
    <div class="select is-primary is-rounded" >
        <select v-model="month" @change="onChange($event)">
            <option v-for="i in 12" :key="i.id" :value="i" >
                {{monthList[i-1]}}
            </option>
        </select>
    </div>
</template>

<script>
export default {
    name: "monthSelector",

    data() {
        return {
            monthList: ["January", "February", "March", "April", "May", "June", "Jule", "August", "September", "October", "November", "December"],
            month: new Date().getMonth()+1,
        }
    },

    props: {
        monthGetter: {type: Function},
    },

    methods: {
        onChange: function(event) {
            this.month = event.target.value;
            this.monthGetter(this.month);
        },

    },

    mounted() {
        // this.monthGetter(this.month);
    }
}
</script>


