<template>
   <tr >
        <th class="people">
            
        </th>
        <!-- {{processWeekBar($parent.firstDateWeekDay)}} -->
        <week v-for="i in 8-weekMinus" :key="i.index" :week_char="weekDayList[weekMinus+i-2]"/>
        <week v-for="i in weekDayList" :key="i.index" :week_char="i"/>
        <week v-for="i in weekDayList" :key="i.index" :week_char="i"/>
        <week v-for="i in weekDayList" :key="i.index" :week_char="i"/>
        <week v-for="i in calculateSuperfluous()" :key="i.index" :week_char="weekDayList[i-1]"/>
        <week v-for="i in calculateFlaw()" :key="i.index" :week_char="weekDayList[i-1]"/>
    </tr>
</template>

<script>

import week from "./weekBarWeek.vue";

export default {
    name: "weekBar",
    
    components: {
        week
    },
    
    data() {
        return {
            weekDayList: ["M", "T", "W", "T", "F", "S", "S"],
        }
    },

    props: {
        weekMinus: Number,
        superfluous: Number,
    },

    methods: {
        calculateSuperfluous: function() {
            if (this.superfluous >= 0) {
                return 7-this.superfluous;
            } else {
                return 7;
            }
        },

        calculateFlaw: function() {
            if (this.superfluous < 0) {
                return -this.superfluous;
            } else {
                return 0;
            }
        }
    },

    
}
</script>

<style>
    .people {
       min-width: 120px;
    }

    
</style>
