<template>
    <tbody class="">
        
        <resource 
            v-for="res in data.resource" :key="res.id"
            :resource_id="res.resource_id"
            :resource_name="res.resource_name"
            :image_URL="res.resource_img"
            :events="selevtAndLoadEvent(res.resource_id)"
        />          
    </tbody>    
</template>

<script>

import resource from "./resourceLine.vue";

export default {
    name: "allResouces",

    components: {
        resource,
    },

    props: {
        data: Object,
        events: Object,
    },

    methods: {
        selevtAndLoadEvent: function(res_id) {
            let pack = [];
            let i = 0;
            for (i in this.events.events) {
                if (this.events.events[i].resource_id == res_id) {
                    pack.push(this.events.events[i]);
                }
            }
            
            
            return pack;
        },

        getDifference: function(date1, date2) {
            //Get 1 day in milliseconds
            var one_day=1000*60*60*24;

            // Convert both dates to milliseconds
            var date1_ms = new Date(date1).getTime();
            var date2_ms = new Date(date2).getTime();

            // Calculate the difference in milliseconds
            var difference_ms = date2_ms - date1_ms;
        
            // Convert back to days and return
            // console.log(Math.round(difference_ms/one_day))
            return Math.round(difference_ms/one_day); 
        },

        array_move: function (arr, old_index, new_index) {
            if (new_index >= arr.length) {
                var k = new_index - arr.length + 1;
                while (k--) {
                    arr.push(undefined);
                }
            }
            arr.splice(new_index, 0, arr.splice(old_index, 1)[0]);
            return arr; // for testing
        },

        sortEventArray: function (eventPack) {
            //console.log(eventPack);
            let fStep = 0;
            let sStep;
            
            if (eventPack.length <= 1) {
                return eventPack;
            }
            // new Date(this.events[i].date_start);
            // new Date(this.events[i].date_end);
            // if ( (startToDate >= from && to >= endToDate) || (startToDate >= from && to <= endToDate) || (startToDate <= from && to >= endToDate)) {}
            for (fStep in eventPack) {
                sStep = 0;
                for (sStep in eventPack) {
                    if (
                        this.getDifference(eventPack[fStep].date_start, eventPack[fStep].date_end)
                        <
                        this.getDifference(eventPack[sStep].date_start, eventPack[sStep].date_end)
                    ) {
                        eventPack = this.array_move(eventPack, fStep, sStep);
                       
                    } 
                } 
            }
            // console.log(this.getDifference(eventPack[fStep].date_start, eventPack[fStep].date_end));
            // console.log(eventPack);
        },
    }

}
</script>

