<template>
    <div class="column" >
        {{calculateDayCount()}}
        <table class="table is-size-7 is-clipped" v-if="rerenderValue">
            <thead>
                <weekBar :weekMinus="processFirstDateWeekDay()" :superfluous="36-processFirstDateWeekDay()-selectedMonthDayCount"/>
                <dateBar/>
            </thead>
            <resourceList :data="data" :events="events"/>
        </table>
        
    </div>
</template>

<script>


import weekBar from "./calendarParts/weekBar.vue";
import resourceList from "./calendarParts/allResources.vue";
import dateBar from "./calendarParts/dateBar.vue";

export default {
    name: "c_calendar",

    data() {
        return {
            selectedMonthDayCount: Number,
            rerenderValue: true,
            firstDateWeekDay: 0,
            
        }
    },

    components: {
        weekBar,
        resourceList,
        dateBar,
        
    },

    props: {
        data: Object,
        events: Object,
        statuses: Array,

        selectedMonth: Number,
        selectedYear: Number,

        singleEventClick: {type: Function},
        doubleEventClick: {type: Function},
        showMoreButton: {type: Function},
        serverDragApprove: {type: Function},

    },

    methods: {
        calculateDayCount: function() {
            this.selectedMonthDayCount = new Date(this.selectedYear, this.selectedMonth, 0).getDate();
            this.firstDateWeekDay = new Date(this.selectedYear, this.selectedMonth-1, 1).getDay();
            
            // console.log(this.selectedMonth);
            // console.log( Math.floor(this.selectedMonthDayCount/2) );
        },

        processFirstDateWeekDay: function() {
            if (this.firstDateWeekDay === 0) {
                return 7;
            } else {
                return this.firstDateWeekDay;
            }
        },

        rerender: function() {
            //this.selectedYear++;
            this.rerenderValue = !this.rerenderValue
            this.$nextTick(() => {
                //this.selectedYear--;
                this.rerenderValue = !this.rerenderValue
            })
        }
    },

    
}
</script>

<style>
    
    

    table {
        margin-bottom: 100px;
    }

    table td {
        /* border-right: 1px dashed rgba(128, 128, 128, 0.267) !important; */
    }
    

</style>
