<template>
    <div 
        :data-resource-id="event.resource_id" 
        :data-event-id="event.event_id" 
        :style="{ background: statusColor}" 
        class="event" 
        @click="click()"
    >
    </div>    
</template>

<script>
export default {
    name: "eventBlock",

    props: {
        statusColor: String,
        event: Object,
        delay: {
            type: Number,
            default: 250
        },
    },

    data() {
        return {
            clickCount: 0,
            clickTimer: null
        }
    },

    methods: {
        click: function() {

            //SINGLE AND DOUBLE CLICK SYSTEM

            this.clickCount++;

                if (this.clickCount === 1) {
                    this.clickTimer = setTimeout(() => {
                    this.clickCount = 0;
                    
                    //this.$parent.$parent.$parent.$parent.$parent.singleEventClick(this.event_name);
                    this.$parent.$parent.$parent.$parent.singleEventClick(this.event);
                    }, this.delay)
                } else if (this.clickCount === 2) {
                    clearTimeout(this.clickTimer);
                    this.clickCount = 0;
                    this.$parent.$parent.$parent.$parent.doubleEventClick(this.event);
                    
                }
        }
    },
}
</script>

<style>
    .event {
        min-width: 13px;
        width: 100%;
        height: 15px;
        margin: 3px 0 0 0;
        cursor: pointer;
        
    }

    .event:hover {
        border: solid 1px #000;
    }

    
</style>
