<template>
  <div id="app">
    
    <h1 class="hero is-primary box">
      <span> This is primary | THIS IS  <s>BULMA</s> BULBA</span>
    </h1>
    <monthSelector :monthGetter="testMonthGetter" class=""/>
    <calendar 
      :data="data" 
      :events="events"
      :statuses="statuses"

      :selectedMonth="selectedMonth"
      :selectedYear="selectedYear"

      :singleEventClick="testSClick"
      :doubleEventClick="testDClick"
      :showMoreButton="testShowMoreButton"
      :serverDragApprove="testServerDragApprove"
    />
    <div class="hero is-primary box">
      {{messageBox}}
    </div>
    <!-- <draggable v-model="data.resource" group="people" @start="drag=true" @end="drag=false">
      <div v-for="element in data.resource" :key="element.id">{{element.resource_name}}</div>
    </draggable>
    <draggable v-model="events.events" group="people" @start="drag=true" @end="drag=false">
      <div v-for="element in events.events" :key="element.id">{{element.event_name}}</div>
