  </div>
</template>

<script>



import resource_list from "./assets/data.json";
import event_list from "./assets/events.json";
import status_list from "./assets/status.json";

import monthSelector from "./components/calendarParts/monthSelector.vue"

import calendar from "./components/c_calendar.vue";

export default {
  name: 'app',

  components: {
    calendar,
    monthSelector,
    
  },

  data() {
      return {
        data: resource_list,
        events: event_list,
        statuses: status_list.statuses,
        messageBox: "",
        selectedYear: new Date().getFullYear(),
        selectedMonth: new Date().getMonth()+1,
      }
  },

  methods: {
    testSClick: function(event) {
      this.messageBox = event.event_name;
    },

    testDClick: function(event) {
      this.messageBox = event.event_name + " 2x";
    },

    testShowMoreButton: function(events) {
      //console.log(events)
      let i = 0
      this.messageBox = "";
      for (i in events) {
          this.messageBox += events[i].event_name + " | ";  
      }
    },

    testMonthGetter: function(month) {
      this.selectedMonth = parseInt(month);
    },

    // testServerDragApprove: function(eventID, fromResID, toResID) {
    //   //console.log("Approved");
    //   return true;      
