<template>
    <td :data-resource-id="resource_id">
        <!-- <draggable :group="dayDate" @end="onEnd" @start="onStart"> -->
            <event 
                v-for="i in checkEventCount()[0]" :key="i.id" 
                :statusColor="checkColorByStatus(i.status_id)" 
                :sort="false"
                :event="i"
            />
        <!-- </draggable> -->
        <button v-if="checkEventCount()[1] && (averageEventDate() == dayDate || checkMonthJump())" 
                class="button showMoreButton" 
                @click="showMoreClick"
        >
            ▼
        </button>
    </td>
</template>

<script>

// import draggable from 'vuedraggable';

import event from "./eventBlock.vue"

export default {
    name: "rDateBlock",

    components: {
        event,
        // draggable,
    },

    data() {
        return {
            basicColor: "#00ffff",
        }
    },

    props: {
        events: Array,
        dayDate: Number,
        blockDate: Date,
        resource_id: Number,

        //dragEventChecker: {type: Function},
    },

    methods: {
        // onStart: function(data) {
        //     // console.log(data);
        //     let draggedEventID = data.item.dataset.eventId;
        //     let i = 0;
        //     let index = 0;
        //     data.item.draggable = true;
        //     for (i in this.events) {
        //         if (draggedEventID == this.events[i].event_id) {
        //             index = i;
        //             break;
        //         }
        //     }

        //     // console.log (this.events[index]);
            
        // },

        onEnd: function(data) {
            // console.log(data);
            // console.log(data.item.dataset.resourceId);
            // console.log(data.item.dataset.eventId);
            // console.log(data.to.parentNode.dataset.resourceId);
            
            let droppedEventID = data.item.dataset.eventId;

            if (data.item.dataset.resourceId != data.to.parentNode.dataset.resourceId) {
                if (this.$parent.$parent.$parent.serverDragApprove()) {

                
                    let i = 0;
                    for (i in this.events) {
                        if (this.events[i].event_id == droppedEventID) {
                            //console.log("DONE")
                            this.events[i].resource_id = data.to.parentNode.dataset.resourceId;
                            // let buff = this.events[i];
                            // this.events.splice(i, 1);
                            // this.events.unshift(buff);

                            this.$parent.$parent.$parent.rerender();
                            
                            
                        }
                    }
                }
            }

        },

        checkColorByStatus: function(status) {
           let i = 0;
           let status_list = this.$parent.$parent.$parent.$parent.statuses
           for (i in status_list) {
               if (status_list[i].id == status) {
                   return status_list[i].color;
               }
           }
        },

        checkEventCount: function() {
            if (this.events.length == 0) {
                return [0, false];
            }
            if (this.events.length > 2) {
                return [[this.events[0], this.events[1]], true];
            }
            return [this.events, false];
        },

       averageEventDate: function() {
           let i = 1;
           let from = new Date(this.events[0].date_start);
           let to = new Date(this.events[0].date_end);
           for (i in this.events) {
               let startToDate = new Date(this.events[i].date_start);
               let endToDate = new Date(this.events[i].date_end);
               if ( (startToDate >= from && to >= endToDate) || (startToDate >= from && to <= endToDate) || (startToDate <= from && to >= endToDate)) {
                   from = startToDate;
                   to = endToDate;
               }
           }
           
           
           let date_from = from.getDate();
           return date_from;
       },

       checkMonthJump: function() {
            let from = new Date(this.events[this.events.length-1].date_start);
            let to = new Date(this.events[this.events.length-1].date_end);
            let thisDate = this.blockDate;
            let thisDateMonthMax = new Date(thisDate.getFullYear(), thisDate.getMonth()+1, 0).getDate();
            
            if (from <= thisDate && thisDate <= to) {
                if (thisDate.getDate() == 1) {
                    return 1;
                } else if (thisDate.getDate() == thisDateMonthMax) {
                    return thisDateMonthMax;
                }
            }
       },

       showMoreClick: function() {
           this.$parent.$parent.$parent.showMoreButton(this.events);
       }
    },

    mounted() {
        this.checkEventCount();
    }
}
</script>

<style>
    .showMoreButton {
        width: 100%;
        height: 15px;
        margin: 3px 0 0 0;
        font-size: 7pt;
    }
</style>
