<template>
    <tr>
        <resourceBlock :resource_img="image_URL" :resource_name="resource_name"/>
        <resourceDateBlock  class="block" 
            v-for="i in $parent.$parent.selectedMonthDayCount" :key="i.id"
            :events="chechEventByDate(i, $parent.$parent.selectedMonth, $parent.$parent.selectedYear)"
            :dayDate="i"
            :blockDate="new Date($parent.$parent.selectedYear, $parent.$parent.selectedMonth-1, i)"
            :resource_id="resource_id"
        />
        
    </tr>
</template>

<script>

import resourceBlock from "./resourceBlock.vue";
import resourceDateBlock from "./resourceDateBlock.vue";

export default {
    name: "rLine",

    components: {
        resourceBlock,
        resourceDateBlock,
    },

    props: {
        image_URL: String,
        resource_name: String,
        resource_id: Number,

        events: Array,
    },
    
    methods: {
        chechEventByDate: function(date, month, year) {
            // console.log(this.events.length);
            let chekingDate = new Date(year, month-1, date);
            let pack = [];
            let from
            let to
            let i = 0;
            for (i in this.events) {
                from = new Date(this.events[i].date_start);
                to = new Date(this.events[i].date_end);
                
                if ( from <= chekingDate && chekingDate <= to ) {
                        pack.push(this.events[i]);
                        
                }
            }
            return pack;
            
            
        },

        
    },
    
}
</script>

<style>
    .block {
        height: 50px;
        padding: 0px !important;
    }
</style>


